    const mcfly = require("../mcfly");
const api = require("../api");
const constants = require("../constants");

const PodcastActions = mcfly.createActions({
    fetchPodcast:function(podcastId){
        console.log("fetchPodcast action called.");
        return new Promise(function(resolve, reject)  {
            api.loaded.then(function()  {
                api.podcasts.getPodcast({podcastId: podcastId}, function(resp)  {
                    console.log("getpodcast response for " + podcastId);
                    console.log(resp);
                    if(resp.status !== 200){
                        reject(resp.statusText);
                        return
                    }
                    resolve({
                        actionType: constants.actionTypes.PODCAST_FETCHED,
                        podcast: resp.obj
                    });
                });
            });
        });
    },
    fetchSubscriptions:function(userId){
        userId = userId || "me"
        return new Promise(function(resolve, reject)  {
            api.loaded.then(function()  {
                api.users.getSubscriptions({userId: userId}, function(resp)  {
                    if(resp.status !== 200){
                        reject(resp.statusText);
                        return
                    }
                    resolve({
                        actionType: constants.actionTypes.SUBSCRIPTIONS_FETCHED,
                        subscriptions: resp.obj,
                        userId: userId
                    })
                })
            });
        });
    },
    fetchingSubscriptions:function(userId){
        return {
            actionType: constants.actionTypes.FETCHING_SUBSCRIPTIONS,
            userId: userId
        }
    },
    fetchPopularPodcasts:function(){
        return new Promise(function(resolve, reject)  {
            api.loaded.then(function()  {
                api.podcasts.query({order:"subscribers"}, function(res)  {
                    resolve({
                        actionType: constants.actionTypes.POPULAR_PODCASTS_FETCHED,
                        podcasts: res.obj
                    });
                });
            });
        });
    }
});

module.exports = PodcastActions;

